.AboutMeComponent{
  position: fixed !important;
  width: 100% !important;
  height: 100% !important;
  z-index: 20000;
  top: 0;
  left: 0 !important;
  background-color: rgba(0,0,0,.05) !important;
  .am-body{
    width: 60vw;
    border-radius: 10px;
    height: 80vh;
    z-index: 10000 !important;
    box-shadow: 3px 3px 5px 5px rgba(0,0,0,.1) !important;
    background-color: #fff;
    margin-left: 20vw;
    margin-top: 10vh;
    position: relative !important;
    @media screen and (max-width: 1080px){
      width: 90vw !important;
      margin-left: 5vw !important;
      height: 95vh !important;
      margin-top: 2.5vh !important;
      padding-top: 25px;
      .question-icon{
        top: calc(2.5vh - 15px)
      }
      .toCenter{
        height: calc(95vh - 30px) !important;
        border-radius: 10px;
        .content{
          .scroll{
            overflow-y: scroll !important;
            padding: 0px !important;
            p{
              width: 95%;
              margin-left: 2.5%;
            }
          }
        }
      }

    }
    .close-icon{
      position: absolute;
      right: 20px;
      top: 20px;
      width: 15px;
      cursor: pointer;
      transition-duration: 0.3s;
      &:hover{
        transition-duration: 0.3s;
        transform: rotate(180deg);
      }
    }
    .question-icon{
      position: absolute;
      left: -15px;
      top: -15px;
      width: 50px;

    }
    .toCenter{
      height: 80vh;
      display: table;
      border-radius: 10px;

    }
    .content{
      word-wrap: break-word;
      display: table-cell;
      vertical-align: middle !important;
      .scroll{
        padding: calc(3em + 40px) 3em 3em 3em;

      }
    }
  }
}
