@import 'variables';
header.HeaderComponent{
  padding: 3em;
  min-height: 100vh;
  background-color: transparent;
  overflow: hidden !important;

  .information{
    margin-top: 10vw;
    position: relative !important;
    word-wrap: break-word !important;
    width: 100%;
    @media screen and (min-width: 765px) and (max-width: 1080px){
      transform: scale(.8) !important;
      margin-top: 0px !important;
    }
    .columns{
      width: 100% !important;
      margin:  0px !important;
      .column:nth-of-type(2){
        .container{
          width: 400px !important;
          @media screen and (max-width: 1080px){
            width: 100% !important;
            margin-left: 0px !important;
          }
        }
        .title.is-1{
          font-weight: 400 !important;
        }
        .btn{
          color: #fff;
          padding: 8px 15px 8px 15px;
          border: none !important;
          border-radius: 10px;
          cursor: pointer;
          transition-duration: 0.3s;
          width: 50% !important;
          margin-right: 5px !important;
          &:focus, &:active{
            outline: none !important;
          }
          &:hover{
            transition-duration: 0.3s;
            transform: scale(1.1);
          }
        }
        @media screen and (max-width: 1080px){
          padding: 1em;
        }

      }
    }
  }
  .shapes{
    div:nth-of-type(1){
      width: 40vw;
      height: 50vw;
      position: absolute;
      top: -23vw !important;
      left: -20vw !important;
      background-image: linear-gradient(to bottom right, $principal-color,$principal-color,transparentize($principal-color, 0.3));
      border-radius: 2em;
      transform: rotate(-30deg);
      z-index: 2;
    }
    div:nth-of-type(2){
      width: 40vw;
      height: 50vw;
      position: absolute;
      top: -30vw !important;
      right: -27vw !important;
      background-image: linear-gradient(to bottom left, $principal-color,$principal-color,transparentize($principal-color, 0.3));
      border-radius: 2em;
      transform: rotate(-30deg);
      z-index: 2;
    }
  }
  .top-layer{
    position: absolute !important;
    z-index: 10 !important;
    width: 100%;
    left: 0 !important;
  }
}
